.footer {
	padding: 75px 0;
	&__wrap {
		display: flex;
		flex-direction: column;
		row-gap: 50px;
	}
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		row-gap: 35px;
	}
	&__social {
		display: flex;
		column-gap: 70px;
	}
	&__info {
		text-align: center;
	}
	&__privacy {
		border-bottom: 1px solid #fff;
		color: #fff;
		font-size: 15px;
		letter-spacing: 0.15px;
	}
	&__copyright {
		display: block;
		margin-top: 15px;
	}
}

.footer-menu {
	&__list {
		display: flex;
		justify-content: space-between;
		column-gap: 20px;
	}
	&__link {
		color: #fff;
		font-family: "Bebas-Neue";
		font-size: 24px;
	}
}

footer .contacts {
	display: flex;
	column-gap: 30px;
	&__item {
		display: block;
		color: #9e9e9e;
		font-weight: 300;
		line-height: 1.25;
	}
	&__link {
		display: block;
		color: #fff;
		font-family: "Bebas-Neue";
		font-size: 24px;
		font-style: normal;
		font-weight: 700;
		line-height: normal;
	}
}

@media (min-width: 1025px) {
	.footer {
		&__content {
			flex-direction: row;
			justify-content: space-between;
			align-items: flex-start;
		}

		&__info {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
		}

		&__copyright {
			margin-top: 0;
		}
	}

	.footer-menu {
		&__list {
			max-width: 275px;
			display: flex;
			flex-wrap: wrap;
			row-gap: 15px;
		}
		& li {
			flex: 0 0 40%;
		}
	}
}
