.slider-section {
	width: 100%;
	padding-bottom: 50px;
}

.slider {
	width: 100%;
	position: relative;
	border-radius: 30px;
	overflow: hidden;
	&__wrapper {
		display: flex;
		width: 100%;
	}
}

.swiper-button-next::after {
	display: none;
}

.slider-buttons {
	display: flex;
	justify-content: end;
	align-items: center;
	justify-content: space-between;
	column-gap: 130px;
	padding: 0 20px;
	height: 9px;
	margin-top: 20px;
}
.swiper-pagination {
	position: static;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 14px;
	&::after {
		content: "";
		position: absolute;
		right: -105px;
		top: 50%;
		transform: translateY(-50%);
		width: 90px;
		height: 2px;
		background-color: #33c2ff;
	}
}

.swiper-pagination-bullet {
	width: 10px;
	height: 10px;
	opacity: 1;
	background-color: #707070;
	transition: all 0.3s ease-in-out;
}

.swiper-pagination-bullet-active {
	width: 24px;
	height: 24px;
	background-color: transparent;
	border: solid 3px #33c2ff;
}

.swiper-button-next {
	flex: 0 0 24px;
	position: static;
	margin-top: 0;
	width: 24px;
	height: 9px;
	background-image: url("../img/icons/arrow-next.svg");
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
}

.slid {
	position: relative;
	flex: 0 0 100%;
	&__img {
		position: relative;
		width: 100%;
		padding-bottom: 100%;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			background-position: left;
			filter: brightness(0.45);
		}
	}
	&__text {
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
	}
	&__title {
		color: #fff;
		font-family: "Bebas-Neue";
		font-size: 10vw;
		line-height: 1;
	}
	&__descr {
		display: none;
	}
	&__btn {
		display: none;
	}
}

@media (min-width: 641px) {
	.slid {
		&__img {
			height: 690px;
			width: 100%;
			padding-bottom: 0;
		}
		&__btn {
			display: block;
			margin-top: 35px;
			padding: 15px 35px;
			color: #fff;
			font-size: 16px;
			font-style: normal;
			font-weight: 600;
			border-radius: 10px;
			background: linear-gradient(rgb(0, 87, 255) 0px, rgb(33, 130, 244) 100%);
			transition: all 0.3s ease-in;
			&:hover {
				background: linear-gradient(rgb(33, 130, 244) 100%, rgb(0, 87, 255) 0px);
			}
		}
	}
}

@media (min-width: 1025px) {
	.slid {
		&__title {
			font-size: 96px;
		}
		&__descr {
			display: block;
			max-width: 400px;
			text-align: justify;
			font-size: 16px;
			font-style: normal;
			font-weight: 300;
			line-height: normal;
		}
	}
}

@media (min-width: 1281px) {
	.slider-section {
		max-width: 1700px;
		padding-left: calc((100% - 1500px) / 2);
	}
}
