.slid-reviews {
	width: 100%;
	max-width: 100%;
	padding: 30px 20px 63px;
	border-radius: 15px;
	background: linear-gradient(#0057ff 0, #2182f4 100%);

	&__person {
		position: relative;
		color: #fff;
	}
	&__img {
		position: absolute;
		top: -60px;
		left: 0;
		width: 100px;
		height: 100px;
		border-radius: 50%;
		border: solid 3px #fff;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}
	&__name {
		padding-left: 120px;
		margin-bottom: 30px;
		font-family: "Bebas-Neue";
		font-size: 26px;
		font-weight: 700;
		text-transform: uppercase;
	}
	&__descr {
		text-align: center;
		font-size: 15px;
		font-weight: 500;
		line-height: normal;
	}
}

@media (min-width: 641px) {
	.slid-reviews {
		max-width: 580px;
		&__name {
			font-size: 32px;
		}
	}
}

