.advantages {
	padding-top: 75px;
	background-image: url(../img/bg.png);
	background-repeat: repeat-x;
	background-position: center -100px;
	& h3 {
		margin-bottom: 30px;
		position: relative;
		display: inline-block;
	}
	&__title-img {
		display: none;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 20px;
	}
	&__item {
		flex: 0 0 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 280px;
		padding: 43px 13px 27px;
		border-radius: 10px;
		color: #fff;
		background: #282828;
		text-align: center;
		transition: all 0.3s ease-in-out;
		&:hover {
			background: linear-gradient(#2182f4 100%, #0057ff 0);
		}
	}
	&__img {
		position: relative;
		width: 109px;
		height: 109px;
		margin-bottom: 43px;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__title {
		margin-bottom: 38px;
		font-weight: 700;
	}
	&__descr {
		font-size: 15px;
		font-weight: 300;
	}
}

@media (min-width: 481px) {
	.advantages {
		&__item {
		flex: 0 0 calc(50% - 10px);
		}
		&__title-img {
			display: block;
			height: 90px;
			position: absolute;
			right: -130px;
			top: 50%;
			transform: translateY(-50%);
			object-fit: contain;
			rotate: 15deg;
		}
	}
}

@media (min-width: 1025px) {
	.advantages {
		&__item {
			flex: 1 0 calc(25% - 20px * 3);
		}
	}
}

@media (min-width: 1281px) {
	.advantages {
		padding-top: 117px;
		& h3 {
			margin-bottom: 80px;
		}
	}
}
