.reviews {
	position: relative;
	padding: 75px 0;
	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		left: 0;
		bottom: -210px;
		width: 100%;
		height: 570px;
		background-image: url(../img/bg.png);
		background-position: center;
		background-repeat: repeat-x;
		background-size: contain;
	}
	& h4 {
		margin-bottom: 60px;
	}
}
@import "_slider-reviews.scss";
