.services {
	padding-top: 50px;
	background-image: url(../img/bg.png);
	background-repeat: repeat-x;
	background-position: center -100px;
	& h2 {
		margin-bottom: 30px;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		row-gap: 50px;
	}
}

.services-mt {
	margin-bottom: 75px;
}

.item {
	position: relative;
	display: flex;
	flex-direction: column;
	&__img {
		position: relative;
		padding-bottom: 60%;
		margin-bottom: 25px;
		width: calc(100% - 12px);
		opacity: 0.5;
		& img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 30px;
			object-fit: cover;
		}
		&::before {
			position: absolute;
			z-index: 2;
			content: "";
			width: 100%;
			height: 100%;
			left: 12px;
			bottom: 13px;
			border-radius: 30px;
			border: 1px solid #0b65fc;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		flex-grow: 1;
	}

	&__title {
		margin-bottom: 10px;
		font-family: "Bebas-Neue";
		font-size: 36px;
		font-weight: 700;
		line-height: 1;
	}
	&__descr {
		margin-bottom: 20px;
		font-family: $fontFamily;
		font-size: 15px;
		font-weight: 300;
	}
}

.btn {
	margin-top: auto;
	align-self: center;
	transition: color 0.3s ease-in;
	&:hover {
		color: #2182f4;
		border: 1px solid #2182f4;
		background-color: #2183f40e;
	}
}

@media (min-width: 641px) {
	.services__list {
		justify-content: center;
		column-gap: 20px;
	}

	.item {
		flex: 1 0 280px;
		max-width: 295px;
		&__title {
			min-height: 70px;
		}
	}
}

@media (min-width: 961px) {
	.item {
		flex: 1 0 290px;
		max-width: 320px;
	}
}

@media (min-width: 1025px) {
	.services {
		& h2 {
			margin-bottom: 85px;
		}
	}
	.item {
		flex: 1 0 290px;
		max-width: 320px;
	}
}

@media (min-width: 1281px) {
	.item {
		flex: 0 0 280px;
		&__img {
			margin-bottom: 34px;
		}
		&__title {
			margin-bottom: 9px;
		}
		&__descr {
			margin-bottom: 37px;
		}
	}
}
