.logo {
	display: flex;
	align-items: center;
	column-gap: 11px;
	&__img {
		position: relative;
		width: 40px;
		height: 44px;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__title {
		display: block;
		color: #fff;
		font-family: Bebas Neue;
		font-size: 31px;
		font-style: normal;
		font-weight: 700;
		line-height: 1;
		& span {
			display: block;
			text-align: center;
			font-family: Montserrat;
			font-size: 16px;
			font-weight: 300;
			line-height: normal;
		}
	}
}

@media (min-width: 1281px) {
	.logo {
		&__img {
			width: 64px;
			height: 71px;
		}
		&__title {
			font-size: 40px;
			& span {
				font-size: 20px;
			}
		}
	}
}
