.portfolio {
	&__wrap {
		.title_h1 {
			margin: 30px 0;
		}
		margin-top: 30px;
	}
	&__nav {
		display: flex;
		flex-direction: column;
		row-gap: 20px;
		padding: 10px 0 30px;
		text-align: center;
		font-weight: 300;
		font-size: 20px;
	}
	&__nav-item {
		padding-bottom: 10px;
		border-bottom: solid #fff 1px;
	}
	&__items {
		display: flex;
		flex-direction: column;
		row-gap: 30px;
	}
	&__item {
	}
	&__item-img {
		position: relative;
		width: 100%;
		padding-bottom: 72%;
		border-radius: 35px;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__descr {
		margin-top: 14px;
		font-size: 15px;
		font-weight: 300;
	}
}

.hide {
	display: none;
}

@media (min-width: 641px) {
	.portfolio {
		&__nav {
			flex-direction: row;
			flex-wrap: wrap;
			row-gap: 30px;
		}
		&__nav-item {
			padding: 0 24px 10px;
		}
		&__items {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: flex-start;
			column-gap: 18px;
			row-gap: 46px;
		}
	}
}
