.client {
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 30px;
		margin-top: 40px;
		padding: 30px;
		border-radius: 10px;
		background: #282828;
	}
	&__item {
		align-self: center;
		position: relative;
		& img {
			scale: 1;
			margin: auto;
		}
		& img:hover {
			transition: scale 1s ease-in-out;
			scale: 1.2;
		}
	}
}
