.case {
	width: 100%;
	overflow: hidden;
	border-radius: 30px;
}
.case__wrapper {
	display: flex;
	width: 100%;
}
.case__slide {
	flex: 0 0 100%;
	height: 350px;
	overflow: hidden;
	& img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

@media (min-width: 1281px) {
	.case {
		width: 580px;
		height: 380px;
	}
	.case__slide{
		height: 100%;
	}
}
