.info {
	&__title {
		margin-bottom: 30px;
		font-family: "Bebas-Neue";
		font-size: 54px;
		line-height: 1;
	}
	&__item {
		font-family: $fontFamily;
		font-size: 15px;
		font-weight: 500;
	}
	&__item:not(:last-child) {
		margin-bottom: 18px;
	}
}

@media (min-width: 481px) {
	.info {
		position: relative;
		&::after {
			content: "";
			position: absolute;
			right: 0;
			top: 0;
			width: 70px;
			height: 100px;
			background-image: url(../img/icons/star.png);
			background-repeat: no-repeat;
			background-size: contain;
			background-position: right;
		}
	}
}

@media (min-width: 961px) {
	.info {
		&::after {
			top: -25px;
		}
		&__title {
			margin-bottom: 42px;
		}
		&__item:not(:last-child) {
			margin-bottom: 30px;
		}
	}
}

@media (min-width: 1281px) {
	.info {
		&::after {
			width: 131px;
			height: 187px;
			top: -40px;
		}
		&__title {
			font-size: 64px;
		}
	}
}
