.preview {
	display: block;
	width: 100%;
}
.preview__wrapper {
	display: flex;
	justify-content: space-between;
	width: 100%;
	overflow: hidden;
}
.preview__slide {
	flex: 0 0 calc(25% - 15px);
	border-radius: 30px;
	overflow: hidden;
	& img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

@media (min-width: 961px) {
	.preview__wrapper {
		height: 145px;
	}
}
@media (min-width: 1281px) {
	.preview__wrapper {
		height: 214px;
	}
}
