.cases {
	padding: 75px 0;
	&__slider-wrap {
		display: flex;
		flex-direction: column;
		row-gap: 30px;
	}
	&__slider {
		margin-top: 40px;
	}
	&__slider-preview {
		display: none;
	}
	&__btn{
		margin-top: 40px;
		text-align: center;
	}
}

@media (min-width: 641px) {
	.cases {
		&__slider-preview {
			display: block;
			margin-top: 40px;
		}
	}
}

@media (min-width: 961px) {
	.cases {
		&__slider-wrap {
			flex-direction: row;
			column-gap: 20px;
			margin-top: 40px;
		}
		&__slider {
			margin-top: 0;
			width: 60%;
		}
		&__info {
			flex: 1 1 40%;
			align-self: center;
		}
	}
}

@media (min-width: 1281px) {
	.cases {
		&__slider-wrap {
			column-gap: 55px;
		}
		&__slider {
			max-width: 580px;
		}
	}
}

@import "_case.scss";
@import "_info.scss";
@import "_preview.scss";
