.modal-overlay {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100%;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}

.modal {
	width: 500px;
	height: 300px;
	display: flex;
	align-items: center;
	justify-content: center;
	display: none;
	background-image: url("../img/bg-form.jpg");
	background-size: cover;
}

.modal-overlay--visible {
	opacity: 1;
	z-index: 50;
	visibility: visible;
	transition: all 0.3s ease-in-out;
}

.modal--visible {
	display: block;
}
.form-modal__title {
	margin-top: 10px;
	text-align: center;
	& span {
		margin: 5px 0 20px;
		display: block;
		font-family: Montserrat;
		font-size: 18px;
		font-weight: 300;
	}
}

