.intro {
	padding-top: 50px;
	background-color: tomato;
	background-repeat: repeat-x;
	background-position: center -100px;
	background-image: url("../img/bg.png");
	background-size: cover;
	background-repeat: repeat-x;
	background-position: center;
	&__wrap {
		display: flex;
		flex-direction: column;
		row-gap: 45px;
	}
	&__img {
		position: relative;
		width: 100%;
		padding-bottom: 100%;
		border-radius: 30px;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__data {
		display: flex;
		flex-direction: column;
		row-gap: 30px;
	}
	&__text {
		max-width: 460px;
		text-align: justify;
		font-size: 20px;
		font-weight: 300;
	}
	&__btn {
		max-width: 300px;
		display: inline-block;
		padding: 15px 35px;
		color: #fff;
		text-align: center;
		font-size: 16px;
		font-style: normal;
		font-weight: 600;
		border-radius: 10px;
		background: linear-gradient(rgb(0, 87, 255) 0px, rgb(33, 130, 244) 100%);
		transition: all 0.3s ease-in;
		&:hover {
			background: linear-gradient(rgb(33, 130, 244) 100%, rgb(0, 87, 255) 0px);
		}
	}
}
@media (min-width: 481px) {
	.intro {
		&__wrap {
			position: relative;
			&::after {
				content: "";
				position: absolute;
				right: 8px;
				bottom: -3px;
				width: 65px;
				height: 65px;
				background-image: url("../img/icons/play.png");
				background-size: contain;
				background-repeat: no-repeat;
			}
		}
	}
}

@media (min-width: 641px) {
	.intro {
		&__wrap {
			&::after {
				width: 100px;
				height: 100px;
			}
		}
	}
}

@media (min-width: 961px) {
	.intro {
		&__wrap {
			flex-direction: row;
			column-gap: 30px;
			&::after {
				right: 8px;
				bottom: 140px;
			}
		}
		&__img {
			height: 530px;
			flex: 0 0 50%;
			padding-bottom: 0;
		}
		&__data {
			padding-top: 45px;
			row-gap: 0;
		}
		&__text {
			margin: 56px 0px 68px;
		}
		&__btn {
			padding: 25px 100px;
		}
	}
}

@media (min-width: 1281px) {
	.intro {
		&__wrap {
			&::after {
				right: -70px;
				bottom: 130px;
				width: 165px;
				height: 165px;
			}
		}
	}
}
