.contacts {
	display: none;
}

@media (min-width: 1281px) {
	.contacts {
		display: flex;
		column-gap: 30px;
		&__item {
			display: block;
			color: #9e9e9e;
			font-weight: 300;
			line-height: 1.25;
		}
		&__link {
			display: block;
			color: #fff;
			font-family: "Bebas-Neue";
			font-size: 24px;
			font-style: normal;
			font-weight: 700;
			line-height: normal;
		}
	}
}
