.type {
	&__wrap {
		margin-top: 75px;
	}
}

.tabs__nav {
	display: flex;
	flex-direction: column;
	row-gap: 15px;
	padding: 30px 0;
}

.tabs__nav-item {
	text-align: center;
}

.tabs__nav-btn {
	width: 100%;
	max-width: 250px;
	padding: 0px 30px 5px;
	border-bottom: #fff solid 1px;
	font-weight: 300;
	color: #fff;
	font-size: 20px;
	background-color: transparent;
}

.tabs__panel {
	display: none;
}

.tabs__panel--active {
	display: block;
}

.tabs__nav-btn:focus {
	color: #0b65fc;
}

.tabs__nav-btn--active {
	color: #0b65fc;
	border-bottom: #0b65fc solid 3px;
	transition: color 0.3s ease-in-out;
	font-weight: 600;
	padding-bottom: 4px;
}

@media (min-width: 641px) {
	.tabs__nav {
		flex-direction: row;
		flex-wrap: wrap;
	}
}

// --CONTENT--
.parent {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: 250px 150px 250px 1fr 250px;
	grid-column-gap: 20px;
	grid-row-gap: 20px;
}

.div1 {
	grid-area: 1 / 1 / 2 / 3;
}
.div2 {
	grid-area: 2 / 1 / 3 / 2;
}
.div3 {
	grid-area: 2 / 2 / 3 / 3;
}
.div4 {
	grid-area: 3 / 1 / 4 / 3;
}
.div5 {
	grid-area: 4 / 1 / 5 / 3;
}
.div6 {
	grid-area: 5 / 1 / 6 / 3;
}

.tabs-item {
	&__content-img {
		position: relative;
		border-radius: 10px;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__text {
		text-align: justify;
		font-size: 14px;
		font-weight: 300;
		line-height: 1.93;
	}
}

@media (min-width: 481px) {
	.parent {
		grid-template-rows: 300px 200px 300px 1fr 300px;
	}
}

@media (min-width: 961px) {
	.parent {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: 200px 200px 300px;
		grid-column-gap: 20px;
		grid-row-gap: 20px;
	}

	.div1 {
		grid-area: 1 / 1 / 3 / 3;
	}
	.div2 {
		grid-area: 1 / 3 / 2 / 4;
	}
	.div3 {
		grid-area: 2 / 3 / 3 / 4;
	}
	.div4 {
		grid-area: 1 / 4 / 3 / 5;
	}
	.div5 {
		grid-area: 3 / 1 / 4 / 3;
	}
	.div6 {
		grid-area: 3 / 3 / 4 / 5;
	}
}
