.video {
	position: relative;
	padding-bottom: 28%;
	aspect-ratio: 16 / 9;
	border-radius: 10px;
	overflow: hidden;
	&:not(:last-child) {
		margin-bottom: 20px;
	}
	&__media {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
	&__button {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 68px;
		height: 48px;
		border: none;
		background-color: transparent;
		transform: translate(-50%, -50%);
	}
}

.video__button {
	outline: none;
}
.video__shepe {
	fill: #212121;
	fill-opacity: 0.8;
}
.video:hover .video__shepe,
.video__button:focus .video__shepe {
	fill: #ff0000;
	fill-opacity: 1;
}

.video--enabled {
	cursor: pointer;
}

.video--enabled .video__button {
	display: block;
}

@media (min-width: 641px) {
	.video:not(:last-child) {
		margin-bottom: 0px;
	}
}
